import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../css/message.css';
export default class MessageDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnStatus: false
        };
    }

    render() {
        return (
            <div>
                {this.state.btnStatus===true ?<div><div class="fade"></div>
                <div class="fade_pop_o">
                    <div class="fade_pop_ow1 ow2"></div>
                    <img src={require('../images/28.png')} class="fade_pop_owimg1" />
                    <div class="fade_pop_ow2">,</div>
                    <Link to="/Login"><div class="fade_pop_ow3"></div></Link>
                    <div class="fade_pop_ow4"  onClick= {this.fadeChange_cancel.bind()}></div>
                </div></div>:''}
                <div class="backHeader">
                <Link to="/IndexShow"><img src={require('../images/23.png')} alt="" class="backImg"/></Link>
                <span>GD Instagram</span>
                </div>
                <div><div class="message_content">
                    <div class="message_content_inner">
                        <div class="message_content_innerLeft">
                            <img src={require('../images/18.png')} alt="" />
                        </div>
                        <div class="message_content_innerRight">
                            <div class="message_r_w1">GD Instagram<div class="focusBtn focusBtnActive"></div></div>
                            <div class="message_r_w2">2020042 18:30</div>
                        </div>
                        <div class="message_r_w3 t8">Singapore will be the very first stop as the rock icons embark on a global stadium tour that includes solo dates in Asia and Europe. Then in June, the band will tour with co-headliners Weezer and Fall Out Boy - The Hella Mega Tour presented by Harley-Davidson, in June then visit Europe, UK and North America, visit cities throughout Europe, the U.K.  and North America.</div>
                        <img src={require('../images/24.png')} alt="" class="pimg"/>
                        <div class="message_content_bottom messagedetail_content_bottom">
                            <img src={require('../images/19.png')} alt="" />
                            <span>60</span>
                            <img src={require('../images/20.png')} alt="" />
                            <span>9532</span>
                        </div>
                    </div>
                    <div class="message_content_comment">
                        <div class="c1">(100)</div>
                        <div class="message_content_commentInner">
                            <img src={require('../images/18.png')} alt="" class="mi_img1"/>
                            <div class="mi">
                                <div class="mi_w1"></div>
                                <div class="mi_w2"></div>
                                <div class="mi_w3">
                                <img src={require('../images/25.png')} alt=""/>
                                <span>2020/04/03 19:32</span>
                                </div>
                            </div>
                        </div>
                        <div class="message_content_commentInner">
                            <img src={require('../images/18.png')} alt="" class="mi_img1"/>
                            <div class="mi">
                                <div class="mi_w1">GD Instagram</div>
                                <div class="mi_w2"></div>
                                <div class="mi_w3">
                                <img src={require('../images/25.png')} alt=""/>
                                <span>2020/04/03 19:32</span>
                                </div>
                            </div>
                        </div>
                        <div class="more"></div>
                    </div>
                    <div class="chat" onClick= {this.fadeChange.bind()}>
                        <input type="text" placeholder=""/>
                        <img src={require('../images/27.png')} alt="" class="c1i"/>
                        <img src={require('../images/26.png')} alt="" class="c2i"/>
                    </div>
                </div></div>


            </div>
        )


    }

    fadeChange= () => {
        this.setState({btnStatus: true});
    }
    fadeChange_cancel= () =>{
        this.setState({btnStatus: false});
    }
}