import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../css/pay.css';
export default class Pay extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnStatus: false,
            menuStatus: false
        };
    }

    render() {
        return (
            <div>
                <div class="backHeader">
                    <Link to="/IndexShow"><img src={require('../images/23.png')} alt="" class="backImg"/></Link>
                    <span></span>
                </div>
                <div class="ownMessageInputWrap">
                    <div class="ownMessageInput">
                        <div class="inputTop"></div>
                        <input type="text" placeholder=""/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop"></div>
                        <input type="text" placeholder="+18595854521"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop"></div>
                        <input type="text" placeholder="397442219@qq.com"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop"></div>
                        <input type="text" placeholder=""/>
                        <div class="sDiv">
                            <span onClick= {this.menuChange.bind()}>1</span>
                            <img src={require('../images/15.png')} onClick= {this.menuChange.bind()}  alt=""/>
                            {this.state.menuStatus===true ?<div class="menuDiv">
                                <div onClick= {this.menuCancelChange.bind()}></div>
                                <div onClick= {this.menuCancelChange.bind()}></div>
                                <div onClick= {this.menuCancelChange.bind()}></div>
                                <div onClick= {this.menuCancelChange.bind()}></div>
                            </div>:''}
                            
                        </div>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop"></div>
                        <input type="text" placeholder=""/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop"></div>
                        <input type="text" placeholder="123456"/>
                    </div>
                </div>
                
                <div class="ownMessageInputWrap btt">
                    <div class="bttTop"><img src={require('../images/38.png')} alt=""/><span></span></div>
                    <div class="ownMessageInput">
                        <div class="inputTop"></div>
                        <input type="text" placeholder=""/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop"></div>
                        <input type="text" placeholder="+430602112225454"/>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop"></div>
                        <div class="dateWrap">
                            <div class=" sDiv_1">
                                <span>07</span>
                                <img src={require('../images/15.png')}  alt=""/>
                            </div>
                            <span>---</span>
                            <div class=" sDiv_1">
                                <span>2020</span>
                                <img src={require('../images/15.png')}  alt=""/>
                            </div>
                            <span></span>
                        </div>
                    </div>
                    <div class="ownMessageInput">
                        <div class="inputTop">CVV</div>
                        <input type="text" placeholder="1234"/>
                    </div>
                </div>
                <div class="exitLogin" ></div>
                <div class="exitLogin pinkBg" ></div>
            </div>
        )


    }
    fadeChange_cancel= () =>{
        this.setState({btnStatus: false});
    }
    menuCancelChange= () =>{
        this.setState({menuStatus: false});
    }
    menuChange= () =>{
        this.setState({menuStatus: true});
    }
    
}