import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../css/search.css';
export default class Search_2 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnStatus: false
        };
    }

    render() {
        return (
            <div>
                <div class="backHeader">
                    <Link to="/IndexShow"><img src={require('../images/23.png')} alt="" class="backImg" /></Link>
                    <span></span>
                </div>
                <div class="searchWrap">
                    <div class="searchInner">
                        <img src={require('../images/31.png')} alt=""/>
                        <input type="text" value="BTS" />
                    </div>
                    <span class="cancel cancelSearch"></span>
                </div>
               
                <div class="searchTitleWrap bt0">
                    <div class="searchTitleH"></div>
                    <div class="searchRes">
                        <img src={require('../images/32.png')} alt=""/>
                        <span></span>
                        <span>1</span>
                    </div>
                    <div class="searchRes">
                        <img src={require('../images/32.png')} alt=""/>
                        <span> Instagram</span>
                        <span>122</span>
                    </div>
                    <div class="searchRes">
                        <img src={require('../images/32.png')} alt=""/>
                        <span> Facebook</span>
                        <span>311</span>
                    </div>
                    <div class="searchRes">
                        <span></span>
                    </div>
                    <div class="searchRes">
                        <span></span>
                    </div>
                </div>
            </div>
        )


    }
    fadeChange_cancel= () =>{
        this.setState({btnStatus: false});
    }
    
}